li.dev-item {
    display: ruby-base;
    background-image: linear-gradient(to left top, rgba(120, 192, 175, 0.3) , rgb(248, 248, 248) 90%, rgba(120, 192, 175, 0.3));
    box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.02);
    border-radius: 14px;
    padding: 15px;
    border-style: ridge;
    border-color: rgba(105, 90, 205, 0.034);
    border-width: 2px;
    display: grid;
    grid-template-rows: 1fr auto;
}

li.dev-item.container {
    border-radius: 14px;
    padding: 14px;
}

li.dev-item:hover {
    border-width: 3.5px;
    cursor: pointer;
}

li.dev-item header {
    display: flex;
    flex-direction: row;
    align-items: center;
}

li.dev-item header img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
}

li.dev-item header .user-info {
    margin-left: 10px;
}

.user-info strong {
    display: block;
    font-size: 16px;
    color: #333;
}

.user-info span {
    font-size: 13px;
    color: rgb(36, 36, 36);
    margin-top: 2px;
}

li.dev-item p {
    color: rgb(46, 46, 46);
    font-size: 16px;
    line-height: 20px;
    margin: 10px 0;
    overflow: hidden;
    white-space: normal;
    height: 1.2em;
    text-overflow: -o-ellipsis-lastline;
}

li.dev-item p:hover {
    height: fit-content;
}

li.dev-item footer {
    position: relative;
    bottom: 0;
    width: 100%;
    grid-row-start: 3;
    grid-row-end: 4;      
}

li.dev-item a {
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
}

li.dev-item a:hover {
    color: #455c0e;
    font-style: italic;
    font-weight: 500;
}