@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-body;
}

html, body, #root {
    height: 100%;
}

body {
    display: inline-table;
    width: 100%;
    background: rgba(85, 69, 156, 0.699);
    box-shadow: inset 0 0 15em rgba(58, 47, 105, 0.911);
    -webkit-font-smoothing: antialiased;
    align-items: center;
}

body, input, button {
    font-family: Roboto, snas-serif;;
}
#app {
    max-width: 120;
    margin: 0 auto;
    padding: 60px 30px;

    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

#app main {
    margin-left: 20px;
}

@media (max-width: 960px) {
    #app {
        align-items: center;
        flex-direction: column;
        margin-right: 20px;
    }

    #app main {
        margin-top: 30px;
        width: 100%;
    }

    #app aside {
        margin-left: 20px;
        width: 90%;
    }
}
aside {
    width: 360px;
    background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(120, 192, 175, 0.3)) , color-stop(90%, rgb(248, 248, 248)), to(rgba(120, 192, 175, 0.3)));
    background-image: linear-gradient(to left top, rgba(120, 192, 175, 0.3) , rgb(248, 248, 248) 90%, rgba(120, 192, 175, 0.3));
    border-style: outset;
    border-color: rgba(105, 90, 205, 0.034);
    border-radius: 10px;
    padding: 30px 20px;
}

aside strong {
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}

aside form {
    margin-top: 30px;
}

aside form .input-block + .input-block {
    margin-top: 20px;
}

aside form .input-group {
    margin-top: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: 0.5fr 0.5fr;
}

aside form .input-group .input-block {
    margin-top: 0;
}

aside form .input-block label {
    color: rgb(82, 82, 82);
    font-size: 14px;
    font-weight: bold;
    display: block;
}

aside form .input-block input {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #eee;
}

aside form button[type=submit] {
    width: 100%;
    border: 0;
    margin-top: 30px;
    background:#7d40e7;
    border-radius: 2px;
    padding: 15px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    -webkit-transition: margin-right 0.5s;
    transition: margin-right 0.5s;
}

aside form button[type=submit]:hover {
    background: #6931ca;
}
main {
    flex: 1 1;
}

main ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    list-style: none;
}

@media (max-width: 650px) {
    main ul {
        grid-template-columns: 1fr;
    }
}
li.dev-item {
    display: ruby-base;
    background-image: -webkit-gradient(linear, right bottom, left top, from(rgba(120, 192, 175, 0.3)) , color-stop(90%, rgb(248, 248, 248)), to(rgba(120, 192, 175, 0.3)));
    background-image: linear-gradient(to left top, rgba(120, 192, 175, 0.3) , rgb(248, 248, 248) 90%, rgba(120, 192, 175, 0.3));
    box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.02);
    border-radius: 14px;
    padding: 15px;
    border-style: ridge;
    border-color: rgba(105, 90, 205, 0.034);
    border-width: 2px;
    display: grid;
    grid-template-rows: 1fr auto;
}

li.dev-item.container {
    border-radius: 14px;
    padding: 14px;
}

li.dev-item:hover {
    border-width: 3.5px;
    cursor: pointer;
}

li.dev-item header {
    display: flex;
    flex-direction: row;
    align-items: center;
}

li.dev-item header img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
}

li.dev-item header .user-info {
    margin-left: 10px;
}

.user-info strong {
    display: block;
    font-size: 16px;
    color: #333;
}

.user-info span {
    font-size: 13px;
    color: rgb(36, 36, 36);
    margin-top: 2px;
}

li.dev-item p {
    color: rgb(46, 46, 46);
    font-size: 16px;
    line-height: 20px;
    margin: 10px 0;
    overflow: hidden;
    white-space: normal;
    height: 1.2em;
    text-overflow: -o-ellipsis-lastline;
}

li.dev-item p:hover {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

li.dev-item footer {
    position: relative;
    bottom: 0;
    width: 100%;
    grid-row-start: 3;
    grid-row-end: 4;      
}

li.dev-item a {
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
}

li.dev-item a:hover {
    color: #455c0e;
    font-style: italic;
    font-weight: 500;
}
